export interface Logger {
    trace?: (...message: any[]) => void;
    debug: (...message: any[]) => void;
    info: (...message: any[]) => void;
    warn: (...message: any[]) => void;
    error: (...message: any[]) => void;
}

export const NullLogger: Logger = {
    debug: () => {},
    error: () => {},
    info: () => {},
    trace: () => {},
    warn: () => {},
};

export function getNullLogger(): Logger {
    return NullLogger;
}
